.limite-container{
    max-width: 1024px;
    margin: 0 auto;
    padding: 0 20px;
}

*{
    margin: 0;
    padding: 0;
    box-sizing: border-box;
    font-family: 'Poppins', sans-serif;
    cursor: default;

}

html {
    scroll-behavior: smooth;
}

html::-webkit-scrollbar {
    display: none;
}